@import 'base';
// @import '~bootstrap/scss/bootstrap.scss'; //example stylesheet load

// Some best-practice CSS that's useful for most apps
// Just remove them if they're not what you want
html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

// Shinobi styles -- remove before using

body{
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

.app {
  max-height: 100vh;
}



